.MiddleContent{
    max-width: 100vw;
    padding: 20px 8%;
}

.MiddleContent img{
    max-width: 100%;
}

.Container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.image{
    flex: 50%;
    max-width: 50%;
    display: flex;
    justify-content: center;
}

.forms{
    flex: 50%;
    max-width: 50%;
    padding: 0 5%;
    display: flex;
    justify-content: center;
}

@media (max-width : 960px){
    .MiddleContent{
        padding: 0 8% 30px 8%;
    }
    .Container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .forms{
        flex: 100%;
        max-width: 100%;
    }
    .image{
        flex: 80%;
        max-width: 80%;
    }
}