.Login{
    height: 310px;
    width: 300px;
    box-shadow: 5px 5px 5px #ccc, -5px -5px 5px #ccc ;
    padding: 20px 5%;
    box-sizing: border-box;
    border-radius: 7px;
}

.Container{
    width: 100%;
    padding: 20px 0;
}

.heading{
    width: 100%;
    text-align: center;
    color: #0c3d39;
}

.heading h2{
    margin: none;
    padding: none;
}

.inputBox{
    width: 100%;
    box-shadow: 2px 2px 10px #ccc;
    border : 2px solid #0c3d3981;
    border-radius: 7px ;  
    display: flex;
    height: 30px;
    margin-top: 15px;
    overflow: hidden;
}

.socialContainer{
    flex: 15%;
    max-width: 15%;
    background-color: #0c3d39;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(238, 238, 238);
}

.inputContainer{
    flex: 75%;
    max-width: 75%;
}

.inputContainer input{
    outline: none;
    width: 100%;
    border: none;
    padding: 8px;
}

.buttonContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

.forgotContainer{
    width: 100%;
    display: flex;
    margin-top: 15px;
    justify-content: space-evenly;
}

.rememberMe{
    display: flex;
    align-items: center;
}

.rememberMe [type="checkbox"]:checked{
    color: #0c3d39;
}

.Error{
    width: 100%;
    text-align: center;
    color: red;
    font-size: 13px;
}

