.Header{
    max-width: 100vw;
    padding: 10px 2%;
    box-shadow: 2px 5px 8px #ccc;
}

.Header h1,h2,h3,h4,h5,h6{
    padding: 0;
    margin: 0;
}

.Container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Logo{
    flex: 15%;
    max-width: 15%;
}

.MenuItems{
    display: flex;
    flex: 40%;
    max-width: 40%;
    justify-content:space-around;
    align-items: center;
}

.MenuItems h5{
    font-weight: 450;
}

.ButtonContainer{
    flex: 13%;
    max-width: 13%;
    display: flex;
    justify-content: space-between;
    height: 30px;
    /* background-color: red; */
}

@media (max-width: 960px) {
    .MenuItems {
        display: none;
    }
    .ButtonContainer{
        display: none;
    }
}
