.Footer{
    max-width: 100vw;
    padding: 15px 7%;
    background-color: #0c3d39;
    color: white;
}

.Footer h1,h2,h3,h4,h5,h6{
    padding: 0%;
    margin: 0%;
}

.Container{
    display: flex;
    justify-content: center;
}

.Heading{
    flex: 20%;
    max-width: 20%;
    display: flex;
    align-items: center;
}

.Location{
    flex: 25%;
    max-width: 25%;
}

.Contact{
    flex: 20%;
    max-width: 20%;
}

.Social{
    flex: 15%;
    max-width: 15%;
}

.socialIcons{
    width: 100%;
}

.Social i{
    margin-right: 25px;
}

@media (max-width : 960px){
    .Container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .Heading{
        justify-content: center;
        flex: 80%;
        max-width: 80%;
        margin-bottom: 15px;
    }
    .Location, .Social, .Contact{
        /* margin-top: px; */
        flex: 80%;
        max-width: 80%;
        text-align: center;
    }
    .Social i{
        margin-right: 15px;
    }
}