.Register{
    height: 330px;
    width: 450px;
    box-shadow: 5px 5px 5px #ccc, -5px -5px 5px #ccc ;
    padding: 20px 5%;
    box-sizing: border-box;
    border-radius: 7px;
}

.Container{
    width: 100%;
    padding: 20px 0;
}

.heading{
    width: 100%;
    text-align: center;
    color: #0c3d39;
}

.heading h2{
    margin: none;
    padding: none;
}

.inputBoxContainer{
    max-width: 100%;
    min-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 10px 0;
}

.inputBox{
    box-sizing: border-box;
    flex: 45%;
    max-width: 45%;
    box-shadow: 2px 2px 10px #ccc;
    border : 2px solid #0c3d3981;
    border-radius: 7px ;  
    display: flex;
    height: 30px;
    margin-top: 15px;
    overflow: hidden;
    border-left: 10px solid #0c3d39;
}


.inputContainer{
    flex: 100%;
    max-width: 100%;
    display: flex;
    align-items: center;

}

.inputContainer input{
    outline: none;
    width: 100%;
    border: none;
    padding: 8px;
}

.buttonContainer{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

.forgotContainer{
    width: 100%;
    display: flex;
    margin-top: 15px;
    justify-content: space-evenly;
}

.rememberMe{
    display: flex;
    align-items: center;
}

.rememberMe [type="checkbox"]:checked{
    color: #0c3d39;
}

.Error{
    width: 100%;
    text-align: center;
    color: red;
    font-size: 13px;
}